import React from "react";
import Card from "react-bootstrap/Card";

class SchoolInfo extends React.Component {
  constructor(props){
    super(props);

    this.state ={
        items: [],
        isLoaded: false
    }
}

componentDidMount(){
  var url = 'https://api.data.gov/ed/collegescorecard/v1/schools?api_key=gzjhyPYHt6iDk3JShTd2Fq6nI9rdMBjzWbpJIcWR&fields=latest.school.name,latest.school.city,latest.school.state,latest.school.school_url,latest.school.degrees_awarded.highest,latest.school.ownership,latest.school.locale,latest.school.carnegie_size_setting';
  url = url+'&school.name='+this.props.school_name;
    fetch(url)
    .then(res => res.json())
    .then(json => {
        this.setState({
            items: json.results[0],
            isLoaded: true
        })
    })
}

  render() {
    const{items, isLoaded} = this.state;
    if(!isLoaded){
      return(
        <div>
          Loading....
        </div>
      )
    }else{
      return (
        <div id="school_info_main">
          <Card style={{margin: "0 auto"}}>
          <Card.Header style={{backgroundColor: "skyblue"}}>School Information</Card.Header>
            {/* <Card.Img variant="top" style={{margin: "0 auto", marginTop: "10px", width: "300px", height: "200px", float: "right"}} src="https://images.unsplash.com/photo-1607237138185-eedd9c632b0b?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxzZWFyY2h8MXx8Y29sbGVnZSUyMGNhbXB1c3xlbnwwfHwwfHw%3D&w=1000&q=80" /> */}
            <Card.Body>
              <h2>{items['latest.school.name']}</h2>
              <div id="about_school">
                  <p>{items['latest.school.city']},{items['latest.school.state']}
                    <br/><a href={items['latest.school.school_url']}>{items['latest.school.school_url']}</a>
                  </p>
                  <div>
                    <table width={'100%'}>
                      <tr>
                        <td>{items['latest.school.degrees_awarded.highest']}</td>
                        <td>{items['latest.school.ownership']}</td>
                        <td>{items['latest.school.locale']}</td>
                        <td>{items['latest.school.carnegie_size_setting']}</td>                        
                      </tr>
                    </table>
                      {/* <h1 style={{marginLeft: '20%'}}>{items['latest.school.degrees_awarded.highest']}</h1>
                      <h1 style={{marginLeft: '20%'}}>{items['latest.school.ownership']}</h1>
                      <h1 style={{marginLeft: '20%'}}>{items['latest.school.locale']}</h1>
                      <h1 style={{marginLeft: '20%'}}>{items['latest.school.carnegie_size_setting']}</h1> */}
                  </div>
              </div>
            </Card.Body>
          </Card>
        </div>
      );
    }
  }
}

export default SchoolInfo;
