import React, { useState } from "react";
import { MDBRow, MDBCol, MDBCard, MDBCardBody } from "mdb-react-ui-kit";
import {
  Checkbox,
  FormControlLabel,
  FormLabel,
  FormGroup,
  TextField,
  Tooltip,
  IconButton,
  Grid,
  InputLabel,
  MenuItem,
  FormControl,
  Select,
  Autocomplete,
  createTheme,
  ThemeProvider,
} from "@mui/material";
import GeoprahicPreference from "./GeopraphicPreference/GeoprahicPreference";
import InfoIcon from "@mui/icons-material/Info";
import FOS from "./fos";
import { Country, State } from "country-state-city";
import { Form } from "react-bootstrap";

function PreferenceMotivation() {
  const [collegeType, setCollegeType] = useState(null);
  const [fieldOfStudy, setFieldOfStudy] = useState(null);
  const [location, setLocation] = useState(null);
  const [regiliousAffliations, setReligiousAffliations] = useState(null);
  const [specializedMission, setSpecializedMission] = useState(null);
  const [schoolSize, setSchoolSize] = useState(null);
  const [urbanicity, setUrbanicity] = useState(null);
  const [reasonsToAttendCollege, setReasonsToAttendCollege] = useState(null);
  const [keyConsiderations, setKeyConsiderations] = useState(null);
  const [locationEnable, setLocationEnable] = useState(true);

  const handleCollegeType = (event) => {
    alert(event.target.value);
    setCollegeType(event.target.value);
  };

  const handleFieldOfStudy = (event) => {
    setFieldOfStudy(event.target.value);
  };

  const handleLocation = (event) => {
    setLocation(event.target.value);
  };

  const handleReligiousAffliations = (event) => {
    setReligiousAffliations(event.target.value);
  };

  const handleSpecializedMission = (event) => {
    setSpecializedMission(event.target.value);
  };

  const handleSchoolSize = (event) => {
    setSchoolSize(event.target.value);
  };

  const handleUrbanicity = (event) => {
    setUrbanicity(event.target.value);
  };

  const handleReasonsToAttendCollege = (event) => {
    setReasonsToAttendCollege(event.target.value);
  };

  const handleKeyConsiderations = (event) => {
    setKeyConsiderations(event.target.value);
  };

  const handlehandleTestMe = (event) => {
    console.log(`cp ${collegeType}`);
    console.log(`fos ${fieldOfStudy}`);
    console.log(`rf ${regiliousAffliations}`);
    console.log(`sm ${specializedMission}`);
    console.log(`ss ${schoolSize}`);
    console.log(`ur ${urbanicity}`);
    console.log(`rta ${reasonsToAttendCollege}`);
    console.log(`kc ${keyConsiderations}`);
  };

  const collegePreferenceOptions = [
    { title: "Public", value: 1 },
    { title: "Private Non-Profit", value: 2 },
    { title: "Private Profit", value: 3 },
  ];

  const religiousAffliationOptions = [
    { title: "African Methodist Episcopal", value: 1 },
    { title: "African Methodist Episcopal Zion Church", value: 2 },
    { title: "American Bapist", value: 3 },
    { title: "American Lutheran", value: 4 },
    { title: "Assemblies of God Church", value: 5 },
  ];

  const specializedMissionsOptions = [
    { title: "Women Only", value: 1 },
    { title: "Men Only", value: 2 },
    { title: "Asian American", value: 3 },
    { title: "Alaska Native", value: 4 },
    { title: "Hispanic-Serving", value: 5 },
    { title: "Historically Black College", value: 6 },
  ];

  const schoolSizesOption = [
    { title: "Small", value: 1 },
    { title: "Medium", value: 2 },
    { title: "Large", value: 3 },
  ];

  const urbanicityOption = [
    { title: "City", value: 1 },
    { title: "Suburban", value: 2 },
    { title: "Town", value: 3 },
    { title: "Rural", value: 4 },
  ];

  const reasonsToAttendCollegeOptions = [
    {
      title: "Career prospects",
      value: 1,
      info: "Finding a good job upon graduationmeasured by income and advancement potential",
    },
    {
      title: "Kinship",
      value: 2,
      info: "Making friends and developing life long bonds",
    },
    {
      title: "Pursue Grad School",
      value: 3,
      info: "Leveraging one's undergraduate degree to pursue graduate school",
    },
  ];

  const keyConsiderationOptions = [
    {
      title: "Affinity",
      value: 1,
      info: "Takes into account factors such as college's reputation, ranking, campus safety, transporation access, weather, and perceived cultural fit",
    },
    { title: "Affordability", value: 2, info: "Whether I/we can afford it " },
    {
      title: "Admissibility",
      value: 3,
      info: "Student's chances of getting admitted ",
    },
  ];

  const theme = createTheme({
    components: {
      MuiFormLabel: {
        styleOverrides: {
          asterisk: {
            color: "#db3131",
            "&$error": {
              color: "#db3131",
            },
          },
        },
      },
    },
  });

  const enableLocation = () => {
    if (locationEnable === true) {
      setLocationEnable(false);
    } else {
      setLocationEnable(true);
    }
  };

  React.useEffect(() => {
    if (locationEnable == false) {
      document.getElementById("location-pref-visible").style.display = "none";
    } else {
      document.getElementById("location-pref-visible").style.display = "block";
    }
  });

  return (
    <ThemeProvider theme={theme}>
      <div>
        <ThemeProvider theme={theme}>
          <MDBCard>
            <MDBCardBody className="px-4">
              <MDBRow>
                <MDBCol md="12">
                  <Autocomplete
                    multiple
                    id="preferences-and-motivations"
                    options={collegePreferenceOptions}
                    getOptionLabel={(option) => option.title}
                    onChange={(event, value) =>
                      console.log(JSON.stringify(value[0]))
                    }
                    filterSelectedOptions
                    sx={{ mb: 2 }}
                    renderInput={(params) => (
                      <TextField {...params} required label="College Type" />
                    )}
                  />
                </MDBCol>
              </MDBRow>
              <MDBRow>
                <MDBCol md="12">
                  <Autocomplete
                    id="field-of-study"
                    options={FOS.Fields}
                    getOptionLabel={(option) => option.FOS}
                    filterSelectedOptions
                    onChange={handleFieldOfStudy}
                    sx={{ mb: 2 }}
                    renderInput={(params) => (
                      <TextField
                        {...params}
                        required
                        value={(option) => option.Fos}
                        label="Field of Study"
                        placeholder="Field of Study"
                      />
                    )}
                  />
                </MDBCol>
              </MDBRow>
              <MDBRow>
                <MDBCol md="12">
                  <FormControl fullWidth>
                    <InputLabel id="religious-affiliation-select-label">
                      Religious Affliations
                    </InputLabel>
                    <Select
                      labelId="religious-affiliation-select-label"
                      id="religious-affiliation-select"
                      value={regiliousAffliations}
                      label="Religious Affliations"
                      sx={{ mb: 2 }}
                      onChange={handleReligiousAffliations}
                      fullWidth
                    >
                      {religiousAffliationOptions.map((option) => (
                        <MenuItem value={option.value}>{option.title}</MenuItem>
                      ))}
                    </Select>
                  </FormControl>
                </MDBCol>
              </MDBRow>
              <MDBRow>
                <MDBCol md="12">
                  <FormControl fullWidth>
                    <InputLabel id="specialized-mission-select-label">
                      Specialized Mission
                    </InputLabel>
                    <Select
                      labelId="specialized-mission-select-label"
                      id="specialized-mission-select"
                      value={specializedMission}
                      label="Specialized Mission"
                      sx={{ mb: 2 }}
                      onChange={handleSpecializedMission}
                    >
                      {specializedMissionsOptions.map((option) => (
                        <MenuItem value={option.value}>{option.title}</MenuItem>
                      ))}
                    </Select>
                  </FormControl>
                </MDBCol>
              </MDBRow>
              <MDBRow>
                <MDBCol md="12">
                  <InputLabel id="location-pref">
                    Location Preference
                  </InputLabel>
                  <MDBCard style={{height: '90px'}}>
                    <MDBCardBody>
                      <MDBRow>
                        <MDBCol md="3">
                          <FormControlLabel
                            control={<Checkbox onChange={enableLocation} />}
                            label="Entire US"
                            labelPlacement="end"
                            sx={{ mb: 2 }}
                          />
                        </MDBCol>
                        <MDBCol md="9" id="location-pref-visible">
                          <Autocomplete
                            id="preffered-location"
                            options={State.getStatesOfCountry("US")}
                            getOptionLabel={(option) => option.name}
                            onChange={(event, value) =>
                              console.log(JSON.stringify(value[0]))
                            }
                            filterSelectedOptions
                            sx={{ mx: 2 }}
                            renderInput={(params) => (
                              <TextField
                                {...params}
                                required
                                label="Choose states..."
                              />
                            )}
                          />
                        </MDBCol>
                      </MDBRow>
                    </MDBCardBody>
                  </MDBCard>
                </MDBCol>
              </MDBRow>
              <MDBRow>
                <MDBCol md="12">
                  <FormControl component="fieldset" sx={{ mb: 2, mt:2 }}>
                    <FormLabel component="legend">School Size</FormLabel>
                    <FormGroup aria-label="position" row>
                      {schoolSizesOption.map((option) => (
                        <FormControlLabel
                          value={option.value}
                          control={<Checkbox />}
                          label={option.title}
                          labelPlacement="end"
                          onChange={handleSchoolSize}
                        />
                      ))}
                    </FormGroup>
                  </FormControl>
                </MDBCol>
              </MDBRow>
              <MDBRow>
                <MDBCol md="12">
                  <FormControl component="fieldset" sx={{ mb: 2 }}>
                    <FormLabel component="legend">Urbanicity</FormLabel>
                    <FormGroup aria-label="position" row>
                      {urbanicityOption.map((option) => (
                        <FormControlLabel
                          value={option.value}
                          control={<Checkbox />}
                          label={option.title}
                          labelPlacement="end"
                          onChange={handleUrbanicity}
                        />
                      ))}
                    </FormGroup>
                  </FormControl>
                </MDBCol>
              </MDBRow>
              <MDBRow>
                <MDBCol md="6">
                  <FormControl component="fieldset" sx={{ mb: 2 }}>
                    <FormLabel component="legend" required>
                      Your reasons to attend college
                    </FormLabel>
                    <FormGroup aria-label="position">
                      {reasonsToAttendCollegeOptions.map((option) => (
                        <Grid direction="row">
                          <FormControlLabel
                            value={option.value}
                            control={<Checkbox />}
                            label={option.title}
                            labelPlacement="end"
                            onChange={handleReasonsToAttendCollege}
                          />
                          <Tooltip
                            style={{ marginLeft: "-20px" }}
                            title={option.info}
                          >
                            <IconButton>
                              <InfoIcon />
                            </IconButton>
                          </Tooltip>
                        </Grid>
                      ))}
                    </FormGroup>
                  </FormControl>
                </MDBCol>
                <MDBCol md="6" sx={{ mb: 2 }}>
                  <FormControl component="fieldset">
                    <FormLabel component="legend" required>
                     Key selection considerations and importance
                    </FormLabel>
                    <FormGroup aria-label="position">
                      {keyConsiderationOptions.map((option) => (
                        <Grid direction="row">
                          <FormControlLabel
                            value={option.value}
                            control={<Checkbox />}
                            label={option.title}
                            labelPlacement="end"
                            onChange={handleKeyConsiderations}
                          />
                          <Tooltip
                            style={{ marginLeft: "-20px" }}
                            title={option.info}
                          >
                            <IconButton>
                              <InfoIcon />
                            </IconButton>
                          </Tooltip>
                        </Grid>
                      ))}
                    </FormGroup>
                  </FormControl>
                </MDBCol>
              </MDBRow>
            </MDBCardBody>
          </MDBCard>
        </ThemeProvider>
      </div>
    </ThemeProvider>
  );
}
export default PreferenceMotivation;
