import React from "react";
import SchoolGraduation from "./SchoolGraduation";
import SchoolInfo from "./SchoolInfo";
import SchoolCost from "./SchoolCost";
import SchoolEarning from "./SchoolEarning";

import "../styles/school_components/school.css";
import { useLocation } from "react-router";

function School() {
  const { state } = useLocation();
  return (
    <div id="school_main">
      <SchoolInfo school_name={state.school_name} />
      <br />
      <SchoolGraduation school_name={state.school_name} />
      <br />
      <SchoolCost school_name={state.school_name} />
      <br />
      <SchoolEarning school_name={state.school_name} />
    </div>
  );
}

export default School;
