import React from 'react';
import './App.css';
import Home from './Home';
import School from './school_components/School';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import Header from './Header/Header';
import Login from './Authentication/Login';
import DemographicInformation from './body/RegisterComponents/DemographicInformation';
import FormBody from './body/FormBody';

class App extends React.Component {
  render(){
    return (
      <Router basename={process.env.PUBLIC_URL}>
        <div class="App">
          <Header /><br />
          <div className='content'>
            <Routes>
              <Route path='/' element={<Home />} />
              <Route path='register' element={<FormBody />} />
              <Route path='register/demographicinfo' element={<DemographicInformation/>}/>
              <Route path='school' element={<School />} />
              <Route path='login' element={<Login />}/>
            </Routes>
          </div>
        </div>
      </Router>
    );
  }
}

export default App;
