import React from "react";
import DropdownTreeSelect from "react-dropdown-tree-select";
import data from "./data.json";
import 'react-dropdown-tree-select/dist/styles.css'
import './mycss.css';

function GeoprahicPreference() {

const assignObjectPaths = (obj, stack) => {
  Object.keys(obj).forEach(k => {
    const node = obj[k];
    if (typeof node === "object") {
      node.path = stack ? `${stack}.${k}` : k;
      assignObjectPaths(node, node.path);
    }
  });
};


const transformObject = (obj = {}) => {
  if (obj && typeof obj === 'object') {
     return Object.keys(obj).map((el) => {
        let children = transformObject(obj[el]); return children ? {
            label: el, children: children } : {
           label: obj[el]
        };
     });
  };
};
  
  assignObjectPaths(data);
  
    return (
      <DropdownTreeSelect data={data} texts={{placeholder: 'Select Location'}}  className="mdl-demo"/>
    );
  
}

export default GeoprahicPreference;
