import React from "react";
import Accordion from "react-bootstrap/Accordion";
import Table from "react-bootstrap/Table";
import { PowerBIEmbed } from 'powerbi-client-react';
import { models } from 'powerbi-client';

class SchoolGraduation extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      items: [],
    };
  }

  componentDidMount() {
    var url =
      "https://api.data.gov/ed/collegescorecard/v1/schools?api_key=gzjhyPYHt6iDk3JShTd2Fq6nI9rdMBjzWbpJIcWR&fields=latest.completion, ,latest.student.retention_rate.four_year.full_time_pooled";
    url = url + "&school.name=" + this.props.school_name;
    fetch(url)
      .then((res) => res.json())
      .then((json) => {
        this.setState({
          items: json.results[0],
          isLoaded: true,
        });
      });
  }

  render() {
    const { items } = this.state;
    var grad_rate =
      items[
        "latest.completion.outcome_percentage_suppressed.all_students.8yr.award_pooled"
      ];
    var swrafy =
      items["latest.student.retention_rate.four_year.full_time_pooled"];

    const filter = {
        $schema: "http://powerbi.com/product/schema#basic",
        target: {
          table: "INST",
          column: "INSTNM",
        },
        operator: "In",
        values: [this.props.school_name],
    };
    return (
      <div>
        <Accordion>
          <Accordion.Item eventKey="0">
            <Accordion.Header>Graduation</Accordion.Header>
            <Accordion.Body>
              <Table borderless style={{ width: "100%" }}>
                <tbody>
                  <tr>
                    <td width={"50%"}>
                      <b>Graduation Rate</b>
                    </td>
                    <td width={"50%"}>
                      <b>Students Who Return After Their First Year</b>
                    </td>
                  </tr>
                  <tr>
                    <td>{grad_rate * 100}</td>
                    <td>{swrafy * 100}</td>
                  </tr>
                  <tr>
                    <td>
                    <PowerBIEmbed
                        embedConfig={{
                          type: "visual", // Supported types: report, dashboard, tile, visual and qna
                          id: "21d8f1db-61b3-408b-bfec-5fbe4613f00e",
                          embedUrl:
                            "https://app.powerbi.com/reportEmbed?reportId=21d8f1db-61b3-408b-bfec-5fbe4613f00e&groupId=e39c9d01-e6a0-403b-8947-c562e6a396b1&w=2&",
                          accessToken:
                            "eyJ0eXAiOiJKV1QiLCJhbGciOiJSUzI1NiIsIng1dCI6IjJaUXBKM1VwYmpBWVhZR2FYRUpsOGxWMFRPSSIsImtpZCI6IjJaUXBKM1VwYmpBWVhZR2FYRUpsOGxWMFRPSSJ9..b-3e_eYsJtrjCvBk7arqPSuIFsrgKBOzfFYamwUEQbv5fYqoy4oumncuh1Yt7QEOWYVZks8FiVKsnEda5ilreuS4ItI9_3JdRvSUofXQBc-a8FGGxzBXbkjWwFI8hLBzp1eFfh8mFu3gQnTBkOEKsdnT-bKWAcNnj0xbHSPKZvLvNZYWiyLLtfDNOYr7aQn_4EnHw1JW_yZilbf4_sUiEAmarQX7A2HWmRwSO1m__eZGfa_Rjxq8JzDKeQtiVN7Ey05SLjzm1xOfT1VIZhTHQTwQddC5pv1_egzZD4GyA3f9KDh-xxuWB0c2khpuOcrrydqH0Bai25Sbyl9CbGhJ6w",
                          tokenType: models.TokenType.Aad,
                          pageName: 'ReportSection',
                          visualName: 'cb930da9e98e6aaaa160',
                          filters: [filter],
                        }}/>
                    </td>
                    <td>
                    <PowerBIEmbed
                        embedConfig={{
                          type: "visual", // Supported types: report, dashboard, tile, visual and qna
                          id: "21d8f1db-61b3-408b-bfec-5fbe4613f00e",
                          embedUrl:
                            "https://app.powerbi.com/reportEmbed?reportId=21d8f1db-61b3-408b-bfec-5fbe4613f00e&groupId=e39c9d01-e6a0-403b-8947-c562e6a396b1&w=2&",
                          accessToken:
                            "eyJ0eXAiOiJKV1QiLCJhbGciOiJSUzI1NiIsIng1dCI6IjJaUXBKM1VwYmpBWVhZR2FYRUpsOGxWMFRPSSIsImtpZCI6IjJaUXBKM1VwYmpBWVhZR2FYRUpsOGxWMFRPSSJ9..b-3e_eYsJtrjCvBk7arqPSuIFsrgKBOzfFYamwUEQbv5fYqoy4oumncuh1Yt7QEOWYVZks8FiVKsnEda5ilreuS4ItI9_3JdRvSUofXQBc-a8FGGxzBXbkjWwFI8hLBzp1eFfh8mFu3gQnTBkOEKsdnT-bKWAcNnj0xbHSPKZvLvNZYWiyLLtfDNOYr7aQn_4EnHw1JW_yZilbf4_sUiEAmarQX7A2HWmRwSO1m__eZGfa_Rjxq8JzDKeQtiVN7Ey05SLjzm1xOfT1VIZhTHQTwQddC5pv1_egzZD4GyA3f9KDh-xxuWB0c2khpuOcrrydqH0Bai25Sbyl9CbGhJ6w",
                          tokenType: models.TokenType.Aad,
                          pageName: 'ReportSectionf09867a5220a162ef847',
                          visualName: '15ce7653e00195c5d4d5',
                          filters: [filter],
                        }}/>
                    </td>
                  </tr>
                </tbody>
              </Table>
            </Accordion.Body>
          </Accordion.Item>
        </Accordion>
      </div>
    );
  }
}

export default SchoolGraduation;
