import React, { useState } from "react";
import { MDBRow, MDBCol, MDBCard, MDBCardBody } from "mdb-react-ui-kit";
import {
  FormControl,
  FormControlLabel,
  FormLabel,
  InputLabel,
  Select,
  Radio,
  MenuItem,
  RadioGroup,
  TextField,
  createTheme,
  ThemeProvider,
  Autocomplete,
} from "@mui/material";
import { AdapterDayjs } from "@mui/x-date-pickers/AdapterDayjs";
import { LocalizationProvider } from "@mui/x-date-pickers/LocalizationProvider";
import { DatePicker } from "@mui/x-date-pickers/DatePicker";
import { City, Country, State } from "country-state-city";

function DemographicInformation() {
  const [firstName, setFirstName] = React.useState(null);
  const [lastName, setLastName] = React.useState(null);
  const [dob, setDob] = React.useState(null);
  const [residencyStatus, setResidencyStatus] = React.useState(null);
  const [country, setCountry] = React.useState("US");
  const [streetAddress, setStreetAddress] = React.useState(null);
  const [city, setCity] = React.useState(null);
  const [state, setState] = React.useState(null);
  const [zipCode, setZipCode] = React.useState(null);
  const [ethenicOrigin, setEthenicOrigin] = React.useState(null);
  const [gender, setGender] = React.useState(null);

  const handletestme = () => {
    console.log(`fn ${firstName}`);
    console.log(`ln ${lastName}`);
    console.log(`dob ${dob}`);
    console.log(`rs ${residencyStatus}`);
    console.log(`c ${country}`);
    console.log(`sa ${streetAddress}`);
    console.log(`st ${state}`);
    console.log(`c ${city}`);
    console.log(`zc ${zipCode}`);
    console.log(`eo ${ethenicOrigin}`);
    console.log(`g ${gender}`);
  };

  const handleFirstName = (event) => {
    setFirstName(event.target.value);
  };
  const handleLastName = (event) => {
    setLastName(event.target.value);
  };
  const handleResidencyStatus = (event) => {
    setResidencyStatus(event.target.value);
    if (event.target.value === 2) {
      document.getElementById("citenship_status").style.display = "block";
    } else {
      document.getElementById("citenship_status").style.display = "none";
    }
  };
  const handleCountry = (event) => {
    setCountry(event.target.value);
  };
  const handleStreetAdress = (event) => {
    setStreetAddress(event.target.value);
  };
  const handleCity = (event) => {
    setCity(event.target.value);
  };
  const handleState = (event) => {
    setState(event.target.value);
  };

  const handleZipCode = (event) => {
    setZipCode(event.target.value);
  };

  const handleEthincOrigin = (event) => {
    setEthenicOrigin(event.target.value);
  };
  const handleGender = (event) => {
    setGender(event.target.value);
  };

  const ethincOriginOptions = [
    { title: "Caucassian", value: 1 },
    { title: "African American", value: 2 },
    { title: "Hispanic", value: 3 },
    { title: "Two Or More Races", value: 4 },
    { title: "Unknown", value: 5 },
    { title: "Non Resident Alien/International", value: 6 },
    { title: "Asian", value: 7 },
    { title: "American Indian/Alaska Native", value: 8 },
    { title: "Native Hawaiian/Pacific Islander", value: 9 },
  ];

  const theme = createTheme({
    components: {
      MuiFormLabel: {
        styleOverrides: {
          asterisk: {
            color: "#db3131",
            "&$error": {
              color: "#db3131",
            },
          },
        },
      },
    },
  });

  return (
    <div>
      <ThemeProvider theme={theme}>
        <MDBCard>
          <MDBCardBody className="px-4">
            <MDBRow>
              <MDBCol md="6">
                <TextField
                  error={firstName}
                  id="student-first-name"
                  label="Student's First Name"
                  value={firstName}
                  variant="outlined"
                  onChange={handleFirstName}
                  required
                  fullWidth
                  sx={{ mb: 2 }}
                />
              </MDBCol>
              <br />
              <MDBCol md="6">
                <TextField
                  id="student-last-name"
                  label="Student's Last Name"
                  value={lastName}
                  onChange={handleLastName}
                  variant="outlined"
                  required
                  fullWidth
                  sx={{ mb: 2 }}
                />
              </MDBCol>
            </MDBRow>
            <MDBRow>
              <MDBCol md="12">
                <LocalizationProvider dateAdapter={AdapterDayjs}>
                  <DatePicker
                    label="Date of Birth"
                    value={dob}
                    onChange={(newValue) => {
                      setDob(newValue);
                    }}
                    renderInput={(params) => (
                      <TextField
                        {...params}
                        required
                        fullWidth
                        sx={{ mb: 2 }}
                      />
                    )}
                  />
                </LocalizationProvider>
              </MDBCol>
            </MDBRow>
            <MDBRow>
              <MDBCol md="5">
                <FormControl fullWidth>
                  <InputLabel id="residency-status-select-label">
                    Residency Status
                  </InputLabel>
                  <Select
                    labelId="residency-status-select-label"
                    id="resideny-status-select"
                    label="residency Status"
                    value={residencyStatus}
                    onChange={handleResidencyStatus}
                    sx={{ mb: 2 }}
                  >
                    <MenuItem value={1}>
                      US Citizen or Permanent Resident
                    </MenuItem>
                    <MenuItem value={2}>International</MenuItem>
                  </Select>
                </FormControl>
              </MDBCol>
              <MDBCol md="7" style={{ display: "none" }} id="citenship_status">
                <Autocomplete
                  id="country-list"
                  options={Country.getAllCountries()}
                  getOptionLabel={(option) => option.name}
                  onChange={(event, value) => {
                    setCountry(value.isoCode)
                    setCity('')
                    setStreetAddress('')
                  }}
                  filterSelectedOptions
                  sx={{ mb: 2 }}
                  renderInput={(params) => (
                    <TextField {...params} required label="Country" />
                  )}
                />
              </MDBCol>
            </MDBRow>
            <MDBRow>
              <MDBCol md="12">
                <TextField
                  id="student-address-line1"
                  label="Street Address"
                  value={streetAddress}
                  onChange={handleStreetAdress}
                  variant="outlined"
                  fullWidth
                  sx={{ mb: 2 }}
                />
              </MDBCol>
            </MDBRow>
            <MDBRow>
              <MDBCol md="4">
                <TextField
                  value={city}
                  onChange={handleCity}
                  label='City'
                  required
                  fullWidth
                  sx={{mb: 2}}/>
              </MDBCol>
              <MDBCol md="4">
                <Autocomplete
                  id="student-address-line1"
                  options={State.getStatesOfCountry(country)}
                  getOptionLabel={(option) => option.name}
                  onChange={(event, value) => setState(value.isoCode)}
                  filterSelectedOptions
                  sx={{ mb: 2 }}
                  renderInput={(params) => (
                    <TextField {...params} required label="State" />
                  )}
                />
              </MDBCol>
              <MDBCol md="4">
                <TextField
                  id="student-address-line1"
                  label={residencyStatus == 2? "Postal Code": "Zip Code"}
                  value={zipCode}
                  onChange={handleZipCode}
                  variant="outlined"
                  type="number"
                  required
                  fullWidth
                  sx={{ mb: 2 }}
                />
              </MDBCol>
            </MDBRow>
            <MDBRow>
              <MDBCol md="12">
                <FormControl fullWidth>
                  <InputLabel id="student-ethinic-origin-label" required>
                    Student's Ethinic Origin
                  </InputLabel>
                  <Select
                    labelId="student-ethinic-origin-label"
                    id="student-ethinic-origin"
                    value={ethenicOrigin}
                    label="StudentsEthinicOrigin"
                    onChange={handleEthincOrigin}
                    required
                    sx={{ mb: 2 }}
                  >
                    {ethincOriginOptions.map((option) => (
                      <MenuItem value={option.value}>{option.title}</MenuItem>
                    ))}
                  </Select>
                </FormControl>
              </MDBCol>
            </MDBRow>
            <MDBRow>
              <MDBCol md="12">
                <FormControl>
                  <FormLabel id="gender-label" required>
                    Gender
                  </FormLabel>
                  <RadioGroup
                    row
                    aria-labelledby="gender-label"
                    name="row-radio-buttons-group"
                    value={gender}
                    onChange={handleGender}
                    sx={{ mb: 2 }}
                  >
                    <FormControlLabel
                      value="female"
                      control={<Radio />}
                      label="Female"
                    />
                    <FormControlLabel
                      value="male"
                      control={<Radio />}
                      label="Male"
                    />
                    <FormControlLabel
                      value="Not binary"
                      control={<Radio />}
                      label="Not Binary"
                    />
                    <FormControlLabel
                      value="Prefer Not to say"
                      control={<Radio />}
                      label="Prefer Not to Say"
                    />
                  </RadioGroup>
                </FormControl>
              </MDBCol>
            </MDBRow>
          </MDBCardBody>
        </MDBCard>
      </ThemeProvider>
    </div>
  );
}

export default DemographicInformation;
