import * as React from "react";
import Box from "@mui/material/Box";
import Stepper from "@mui/material/Stepper";
import Step from "@mui/material/Step";
import StepLabel from "@mui/material/StepLabel";
import Button from "@mui/material/Button";
import DemographicInformation from "./RegisterComponents/DemographicInformation";
import FinancialInformation from "./RegisterComponents/FinancialInformation";
import AcademicProfile from "./RegisterComponents/AcademicProfile";
import PreferenceMotivation from "./RegisterComponents/PreferencesAndMotivation";


const steps = [
  "Demographic Info",
  "Academic Profile",
  "Financial Info",
  "Preference & Motivation"
];

export default function FormBody() {
  const [activeStep, setActiveStep] = React.useState(0);

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  return (
    <Box sx={{ width: "90%" ,marginBottom: '5%', marginLeft: '5%', marginRight: '5%'}}>
     <Stepper activeStep={activeStep} id='stepper-curtain' style={{overflow: 'scroll', backgroundColor: 'lightgray',borderRadius:'5px', padding: '5px', position: 'sticky', top: '11%', zIndex: '2'}}>
        {steps.map((label) => {
          const stepProps = {};
          const labelProps = {};
          return (
            <Step key={label} {...stepProps}>
              <StepLabel {...labelProps}>{label}</StepLabel>
            </Step>
          );
        })}
      </Stepper>
      <br/>
      {activeStep === 0 ? (
        <div>
          <DemographicInformation />
          <Box sx={{ display: "flex", flexDirection: "row", pt: 2 }}>
            <Button
              color="inherit"
              disabled={activeStep === 0}
              onClick={handleBack}
              sx={{ mr: 1 }}
            >
              Back
            </Button>
            <Box sx={{ flex: "1 1 auto" }} />
            <Button onClick={handleNext} id="next-btn">
              {activeStep === steps.length - 1 ? "Finish" : "Next"}
            </Button>
          </Box>
        </div>
      ) : activeStep === 1 ? (
        <div>
          <AcademicProfile />
          <Box sx={{ display: "flex", flexDirection: "row", pt: 2 }}>
            <Button
              color="inherit"
              disabled={activeStep === 0}
              onClick={handleBack}
              sx={{ mr: 1 }}
            >
              Back
            </Button>
            <Box sx={{ flex: "1 1 auto" }} />

            <Button onClick={handleNext}>
              {activeStep === steps.length - 1 ? "Finish" : "Next"}
            </Button>
          </Box>
        </div>
      ) : activeStep === 2 ? (
        <div>
          <FinancialInformation />
          <Box sx={{ display: "flex", flexDirection: "row", pt: 2 }}>
            <Button
              color="inherit"
              disabled={activeStep === 0}
              onClick={handleBack}
              sx={{ mr: 1 }}
            >
              Back
            </Button>
            <Box sx={{ flex: "1 1 auto" }} />

            <Button onClick={handleNext}>
              {activeStep === steps.length - 1 ? "Finish" : "Next"}
            </Button>
          </Box>
        </div>
      ) : activeStep === 3 ? (
        <div>
          <PreferenceMotivation />
          <Box sx={{ display: "flex", flexDirection: "row", pt: 2 }}>
            <Button
              color="inherit"
              disabled={activeStep === 0}
              onClick={handleBack}
              sx={{ mr: 1 }}
            >
              Back
            </Button>
            <Box sx={{ flex: "1 1 auto" }} />

            <Button onClick={handleNext}>
              {activeStep === steps.length - 1 ? "Finish" : "Next"}
            </Button>
          </Box>
        </div>
      ) : (
        <h3> You are Finished, Thank you !!</h3>
      )}
    </Box>
  );
}
